/* Two-column layout for NBSTUP Membership Account page */
.pmpro-nbstup-account-layout {
  display: flex;
  gap: 32px;
  align-items: flex-start;
  margin: 20px 0;
}

.pmpro-nbstup-account-sidebar {
  flex: 0 0 280px;
  max-width: 280px;
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  padding: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 20px;
}

.pmpro-nbstup-account-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pmpro-nbstup-account-nav li + li {
  margin-top: 4px;
}
.pmpro-nbstup-account-nav a {
  display: block;
  padding: 12px 16px;
  border-radius: 6px;
  color: #374151;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}
.pmpro-nbstup-account-nav a:hover, .pmpro-nbstup-account-nav a:focus {
  background-color: #f3f4f6;
  color: #111827;
  border-color: #d1d5db;
}
.pmpro-nbstup-account-nav a.active {
  background-color: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}

.pmpro-nbstup-account-content {
  flex: 1 1 auto;
  min-width: 0;
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  padding: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Deceased Members Table Styles */
.pmpro-nbstup-deceased-table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}
.pmpro-nbstup-deceased-table th,
.pmpro-nbstup-deceased-table td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid #e5e7eb;
}
.pmpro-nbstup-deceased-table th {
  background-color: #f9fafb;
  font-weight: 600;
  color: #374151;
  border-bottom: 2px solid #d1d5db;
}
.pmpro-nbstup-deceased-table tbody tr:hover {
  background-color: #f9fafb;
}
.pmpro-nbstup-deceased-table .button {
  background-color: #3b82f6;
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  transition: background-color 0.2s ease;
}
.pmpro-nbstup-deceased-table .button:hover {
  background-color: #2563eb;
}

/* Pagination Styles */
.pmpro-nbstup-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 24px;
}
.pmpro-nbstup-pagination .button {
  background-color: #6b7280;
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  transition: background-color 0.2s ease;
}
.pmpro-nbstup-pagination .button:hover {
  background-color: #4b5563;
}
.pmpro-nbstup-pagination span {
  color: #6b7280;
  font-weight: 500;
}

@media (max-width: 1024px) {
  .pmpro-nbstup-account-layout {
    gap: 24px;
  }
  .pmpro-nbstup-account-sidebar {
    flex: 0 0 240px;
    max-width: 240px;
    padding: 20px;
  }
}
@media (max-width: 768px) {
  .pmpro-nbstup-account-layout {
    flex-direction: column;
    gap: 20px;
    margin: 10px 0;
  }
  .pmpro-nbstup-account-sidebar {
    max-width: 100%;
    position: static;
    padding: 16px;
  }
  .pmpro-nbstup-account-nav a {
    padding: 10px 12px;
  }
  .pmpro-nbstup-account-content {
    padding: 16px;
  }
  .pmpro-nbstup-deceased-table th,
  .pmpro-nbstup-deceased-table td {
    padding: 8px 12px;
  }
}